

































































	import * as echarts from '../../common/echarts/echarts.simple.min.js';
	import mpvueEcharts from '../../common/echarts/src/echarts.vue';
	import pageTabber from '../common/tabber.vue';
	function getPieOption() {
		return {
			animation: false,
			backgroundColor: '#ffffff',
			color: ['#37A2DA', '#32C5E9', '#67E0E3', '#91F2DE', '#FFDB5C', '#FF9F7F'],
			series: [{
				label: {
					normal: {
						fontSize: 12,
						position:'inner',   //标签的位置
					}
				},
				type: 'pie',
				center: ['50%', '50%'],
				radius: [0, '60%'],
				data: [{
					value: 55,
					name: '北京'
				}, {
					value: 20,
					name: '武汉'
				}, {
					value: 10,
					name: '杭州'
				}, {
					value: 20,
					name: '广州'
				}, {
					value: 38,
					name: '上海'
				}],
				itemStyle: {
					emphasis: {
						shadowBlur: 10,
						shadowOffsetX: 0,
						shadowColor: 'rgba(0, 2, 2, 0.3)'
					}
				}
			}]
		}
	}
	
	function getDiug(){
		return {    
			calculable : true,  
			series : [{  
				name:'公里总里程',  
				type:'pie',  
				radius: ['30%', '60%'], //饼图的内圈
				center: ['50%', '50%'], //饼图的位置 
				label:{                 //饼图图形上的文本标签
					normal:{
						show:true,
						position:'inner',   //标签的位置
						textStyle : {
							fontWeight : 300 ,
							fontSize : 12     //文字的字体大小
						},
						formatter:'{d}%'    //显示的文字格式当前为百分比
					}
				},
				data:[  
					{value:50,name:'高速50KM',itemStyle:{normal:{color:'#FE0000'}}},  
					{value:150,name:'一级150KM',itemStyle:{normal:{color:'#F29700'}}},  
					{value:150,name:'二级150KM',itemStyle:{normal:{color:'#02B0ED'}}}, 
					{value:100,name:'三级100KM',itemStyle:{normal:{color:'#55E87D'}}},
					{value:50,name:'四级50KM',itemStyle:{normal:{color:'#FFE200'}}},
				]
			}]  
		}
	}

	export default {
		data() {
			return {
				echarts,
				pieInit: function (canvas, width, height) {
					let pieChart = echarts.init(canvas, null, {
						width: width,
						height: height
					})
					canvas.setChart(pieChart)

					pieChart.setOption(getPieOption())
					return pieChart
				},
				DiugInit: function (canvas, width, height) {
					let pieChart = echarts.init(canvas, null, {
						width: width,
						height: height
					})
					canvas.setChart(pieChart)

					pieChart.setOption(getDiug())
					return pieChart
				},
				active: 0,
				height: 0,
				width: 0,
				title: 'tabbar',
				scrollLeft: 0,
				isClickChange: false,
				currentTab: 0,
				tabs: [{
					name: '关注',
					id: 'guanzhu'
				}, {
					name: '推荐',
					id: 'tuijian'
				}, {
					name: '体育',
					id: 'tiyu'
				}, {
					name: '热点',
					id: 'redian'
				}, {
					name: '财经',
					id: 'caijing'
				}, {
					name: '娱乐',
					id: 'yule'
				}],
				newsitems: [],
				x: 0,
				y: 0,
				old: {
					x: 0,
					y: 0
				}
			}
		},
		components: {
			mpvueEcharts,
			pageTabber
		},
		onLoad: function () {
			this.newsitems = this.randomfn()
		},
		onUnload:function(){
			this.scrollLeft = 0,
			this.isClickChange = false,
			this.currentTab = 0;
		},
		methods: {
			scroll: function (e) {
				console.log(e)
			},
      tapFn: function(){
        uni.switchTab({
          url: '../speed/speed'
        });
      },
			goBrowser() {




				uni.showModal({
					content:"请复制链接在浏览器里打开",
					showCancel:false
				})

			},
			/* 防止第二页侧滑动 */
			stopTouchMove: function() { 
				return false;
			},
			tap: function(e) {
				// 解决view层不同步的问题
				this.x = this.old.x
				this.y = this.old.y
				this.$nextTick(function() {
						this.x = 30
						this.y = 30
				})
			},
			onChange: function(e) {
				this.old.x = e.detail.x
				this.old.y = e.detail.y
			},
			/* 窗口滑动改变tabber样式 */ 
			swiperChange: function(e) {
				this.active = e.detail.current;
				
			},
			/* 第一页处理事件 */
			bindChange: async function (e) {
				let index = e.target.current;
				if (this.isClickChange) {
					this.currentTab = index;
					this.isClickChange = false;
					return;
				}
				let tabBar = await this.getWidth("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft;

				let width = 0;

				for (let i = 0; i < index; i++) {
					let result = await this.getWidth(this.tabs[i].id);
					width += result.width;
				}

				let winWidth = uni.getSystemInfoSync().windowWidth,
					nowElement = await this.getWidth(this.tabs[index].id),
					nowWidth = nowElement.width;

				if (width + nowWidth - tabBarScrollLeft > winWidth) {
					this.scrollLeft = width + nowWidth - winWidth;
				}
				if (width < tabBarScrollLeft) {
					this.scrollLeft = width;
				}
				this.isClickChange = false;
				this.currentTab = index; //一旦访问data就会出问题
			},
			/* 得到元素的宽高 */
			getWidth: function (id) { 
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						if (id === 'tab-bar') {
							console.log("id=", id, "数据:", data)
						}
						res(data);
					}).exec();
				})
			},
			/* 点击tab-bar */
			swichNav: async function (e) { 
				if (this.currentTab === e.target.dataset.current) {
					return false;
				} else {
					let tabBar = await this.getWidth("tab-bar"),
						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.currentTab = e.target.dataset.current
				}
			},
			/* 加载更多数据 */
			loadMore: function (e) {
				let last = this.newsitems[e][this.newsitems[e].length - 1].label,
					name = this.newsitems[e][this.newsitems[e].length - 1].name;
				for (let i = 1; i <= 10; i++) {
					this.newsitems[e].push({
						name: name,
						label: i + last
					});
				}
			},
			/* 处理虚假数据 */
			randomfn:function() {
				let ary = [];
				for (let i = 0, length = this.tabs.length; i < length; i++) {
					let aryItem = [];
					for (let j = 1; j <= 20; j++) {
						aryItem.push({
							name: this.tabs[i].name,
							label: j
						});
					}
					ary.push(aryItem);
				}
				return ary;
			}
		},
		mounted(){
			uni.getSystemInfo({
				success:res=>{
					this.height = res.screenHeight-52;
				}
			})
		}
	}
	



